.navbar {
  height: 223px;
  box-sizing: border-box;

  @media (min-width: $screen-m) {
    height: 56px;
  }

  &__container {
    @include content-area();
  }

  &__btn-wrap {
    //      nav-toggle, .. 
    position: relative;
    z-index: 2;
    float: right;
    margin-top: 16px;

    @media (min-width: $screen-m) {
      display: none;
    }
  }

  &__nav-wrapper {

    @media (max-width: $screen-m - 1) {
      //    nav-toggle,      ,
      //  nav-toggle      
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
    }
  }
}
